body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Open Sans", "Roboto", "Arial", sans-serif;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1150px;
}

.nojs .main-navigation__toggle {
  display: none;
}

.button {
  display: block;
  padding: 14px 15px;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  color: #444444;
  background-color: transparent;
  border: 2px solid #444444;
  transition-duration: 0.15s;
}

.button:hover {
  cursor: pointer;
  color: #ffffff;
  background-color: #444444;
}

.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.button--light {
  padding: 16px 20px;
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
}

.button--light:hover {
  color: #ffffff;
  background-color: #63d1bb;
  opacity: 0.8;
}

.button--light:active {
  color: #ffffff;
  background-color: #63d1bb;
  opacity: 0.6;
}

.logo {
  padding: 15px;
  transition-duration: 0.15s;
}

.logo[href]:hover {
  opacity: 0.6;
}

.logo[href]:active {
  opacity: 0.3;
}

.logo__image {
  display: block;
}

.logo--footer {
  display: none;
}

.logo--footer .logo__icon {
  transition-duration: 0.15s;
}

.logo--footer[href]:hover {
  opacity: 1;
}

.logo--footer[href]:hover .logo__icon {
  fill: #46c1ae;
}

.logo--footer[href]:active {
  opacity: 1;
}

.logo--footer[href]:active .logo__icon {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .logo {
    padding: 0;
  }
  .logo--footer {
    display: block;
  }
}

