.main-navigation__box {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  padding: 0 30px;
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #ececec;
}

.main-navigation__toggle {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 40px;
  height: 40px;
  font-size: 0;
  background-color: transparent;
  border: none;
}

.main-navigation__toggle:hover {
  cursor: pointer;
  opacity: 0.6;
}

.main-navigation__toggle:active {
  opacity: 0.3;
}

.main-navigation__toggle-icon {
  position: absolute;
  display: block;
  width: 20px;
  height: 2px;
  background-color: #221f1f;
}

.main-navigation__toggle-icon::before {
  position: absolute;
  transform: translateY(-6px);
  display: block;
  content: "";
  width: 20px;
  height: 2px;
  background-color: #221f1f;
}

.main-navigation__toggle-icon::after {
  position: absolute;
  transform: translateY(6px);
  display: block;
  content: "";
  width: 20px;
  height: 2px;
  background-color: #221f1f;
}

.main-navigation__toggle--active .main-navigation__toggle-icon {
  width: 28px;
  background-color: transparent;
}

.main-navigation__toggle--active .main-navigation__toggle-icon::before {
  width: 28px;
  transform: rotate(-45deg);
}

.main-navigation__toggle--active .main-navigation__toggle-icon::after {
  width: 28px;
  transform: rotate(45deg);
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
}

.main-navigation__list--show {
  display: block;
}

.main-navigation__sublist {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-navigation__sublist--links .main-navigation__subitem:nth-child(3) {
  display: none;
}

.main-navigation__sublist--links .main-navigation__subitem:nth-child(4) {
  display: none;
}

.main-navigation__subitem {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 5px 30px;
  border-bottom: 1px solid #ececec;
}

.main-navigation__link {
  position: relative;
  display: block;
  width: 100%;
  padding: 22px 10px 22px 45px;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: #000000;
}

.main-navigation__link[href]:hover {
  color: rgba(0, 0, 0, 0.6);
}

.main-navigation__link[href]:hover .main-navigation__icon {
  fill: #63d1bb;
  opacity: 1;
}

.main-navigation__link[href]:active {
  color: rgba(0, 0, 0, 0.3);
}

.main-navigation__link[href]:active .main-navigation__icon {
  fill: #63d1bb;
  opacity: 0.3;
}

.main-navigation__icon {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  opacity: 0.2;
  transition-duration: 0.15s;
}

@media (min-width: 425px) {
  .main-navigation__link {
    margin: 0 auto;
    width: auto;
    padding-left: 45px;
  }
}

@media (min-width: 768px) {
  .main-navigation {
    display: flex;
    padding: 0 50px;
  }
  .main-navigation__box {
    margin-right: -112px;
    padding: 6px 0 0;
    height: 88px;
    flex-shrink: 0;
    border: none;
  }
  .main-navigation__toggle {
    display: none;
  }
  .main-navigation__list {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  .main-navigation__sublist {
    display: flex;
    height: 100%;
  }
  .main-navigation__sublist--links {
    position: absolute;
    top: 88px;
    flex-wrap: wrap;
    flex-grow: 1;
    height: auto;
    max-width: 350px;
  }
  .main-navigation__sublist--links .main-navigation__subitem {
    border-left: none;
  }
  .main-navigation__sublist--links .main-navigation__subitem:first-child {
    margin-right: 37px;
  }
  .main-navigation__link {
    padding: 17px 0;
    font-size: 15px;
    line-height: 24px;
  }
  .main-navigation__subitem {
    padding: 0;
    border-bottom: none;
    border-left: 1px solid #ececec;
  }
  .main-navigation__link--search {
    font-size: 0;
    padding: 30px 40px;
  }
  .main-navigation__link--search .main-navigation__icon {
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .main-navigation__link--cart {
    padding: 30px 0 30px 84px;
    font-size: 17px;
  }
  .main-navigation__link--cart .main-navigation__icon {
    left: 32px;
  }
}

@media (min-width: 1150px) {
  .main-navigation {
    position: relative;
  }
  .main-navigation__box {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  .main-navigation__list {
    width: 100%;
    margin-left: 0;
  }
  .main-navigation__sublist--links {
    position: static;
    flex-wrap: wrap;
    height: 88px;
    margin-left: -35px;
    max-width: 450px;
  }
  .main-navigation__sublist--links .main-navigation__subitem:first-child {
    margin-left: 0;
    margin-right: 0;
    border-right: 1px solid #ececec;
  }
  .main-navigation__sublist--links .main-navigation__subitem:nth-child(3) {
    display: block;
  }
  .main-navigation__sublist--links .main-navigation__subitem:nth-child(4) {
    display: block;
  }
  .main-navigation__link {
    margin: 0;
    font-size: 17px;
    padding: 32px 35px;
  }
  .main-navigation__link--small {
    padding: 17px 26px 17px 35px;
    font-size: 15px;
  }
  .main-navigation__link--search {
    font-size: 0;
    padding: 30px 41px;
  }
  .main-navigation__link--cart {
    margin-right: -33px;
    padding-left: 83px;
  }
}

.categories__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}

.categories__item {
  width: 100%;
}

.categories__link {
  position: relative;
  display: block;
  width: 100%;
  min-height: 103px;
  padding: 25px 120px 25px 30px;
  font-size: 19px;
  line-height: 24px;
  font-weight: bold;
  color: #ffffff;
  transition-duration: 0.15s;
}

.categories__link::before {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  content: "";
  width: 100%;
  height: 6px;
  background-color: inherit;
  transition-duration: 0.15s;
}

.categories__link:hover, .categories__link:focus {
  outline: none;
  transform: translateY(-5px);
}

.categories__link:hover::before, .categories__link:focus::before {
  transform: translateY(5px);
}

.categories__link:hover .categories__icon, .categories__link:focus .categories__icon {
  opacity: 0.7;
  transition-duration: 0.15s;
}

.categories__link:active {
  color: rgba(255, 255, 255, 0.3);
}

.categories__link:active .categories__icon {
  opacity: 0.3;
}

.categories__icon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  fill: #149080;
  opacity: 0.5;
}

.categories__link--interior {
  background-color: #63d1bb;
}

.categories__link--interior .categories__icon {
  right: 36px;
}

.categories__link--interior:hover {
  background-color: #59d5bc;
}

.categories__link--interior:active {
  background-color: #59d5bc;
}

.categories__link--toys {
  background-color: #56c3b2;
}

.categories__link--toys .categories__icon {
  right: 27px;
}

.categories__link--toys:hover {
  background-color: #46c1ae;
}

.categories__link--toys:active {
  background-color: #46c1ae;
}

@media (min-width: 600px) {
  .categories__item {
    display: flex;
    width: 50%;
  }
}

@media (min-width: 768px) {
  .categories {
    position: relative;
    background-color: #fafafa;
  }
  .categories::before {
    position: absolute;
    top: calc(100% - 10px);
    left: 0;
    z-index: 2;
    content: "";
    width: 100%;
    height: 23px;
    background-image: url("../img/bg-zigzag-fill.svg");
    background-size: 768px;
    background-repeat: repeat-x;
    background-position: center bottom;
  }
  .categories__content {
    padding: 0 50px;
  }
  .categories__item {
    width: 50%;
  }
  .categories__link {
    padding: 28px 60% 29px 35px;
    font-size: 20px;
    line-height: 30px;
  }
  .categories__link:hover {
    z-index: 0;
  }
  .categories__link--toys {
    background-color: #56c3b2;
  }
  .categories__link--toys .categories__icon {
    right: 35px;
  }
}

@media (min-width: 1150px) {
  .categories::before {
    top: calc(100% - 18px);
    height: 40px;
    background-size: 1250px;
  }
}

